.navbar {
    background: rgb(30, 30, 30);
    height: 5rem;
    width: 100vw;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo {
    object-fit: cover;
    height: 4rem;
    width: 4rem;
}

.desktopMenuListItem {
    margin: 1rem;
    cursor: pointer;

}

.desktopMenuListItem:hover {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.desktopMenuBtn {
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 1.5rem;
    border-radius: 2rem;

}

.desktopMenuImg {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 0 0.25rem;

}

.Active {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.NavMenu {
    position: absolute;
    top: 4.6rem;
    right: 1.5rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}

.ListItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    /* border-radius: 1rem;  */
    background: rgb(30, 30, 30);
} 

.MoblieMenu {
    display: none;
    object-fit: cover;
    height: 2rem;
}

@media screen and (max-width: 720px) {
    .desktopMenu {
        display: none;
    }

    .MoblieMenu {
        display: flex;
    }

    .desktopMenuBtn {
        display: none;
    }


}
    
